/* BASE QUIZ STYLES */
/* These styles ensure that the necessary elements are hidden for toggling */
/* DO NOT EDIT UNLESS YOU KNOW WHAT YOU ARE DOING!! */
.startQuiz,
.nextQuestion,
.backToQuestion,
.questions li.question,
.questions li.question .responses,
.questions li.question .responses .correct,
.questions li.question .responses .incorrect,
.quizResults {
  display: none;
}

/* If response messages are disabled or only shown on quiz completion,
   nextQuestion button IS checkAnswer button - so it must be displayed */
.nextQuestion.checkAnswer {
  display: block;
}

ol.questions,
ul.answers,
ul.responses,
ol.questions li,
ul.answers li,
ul.responses li {
  list-style-type: none;
}

/* Accessibility */
.quizName span:first-child {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.button {
  float: left;
  width: auto;
  padding: 5px 15px;
  color: #ffffff;
  background-color: darkcyan;
  border: 1px solid #fff;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  text-decoration: none;
}

.button:hover {
  background-color: darkslategray;
}

.startQuiz {
  margin-top: 40px;
}

.tryAgain {
  float: none;
  margin: 20px 0;
}

/* clearfix */
.quizArea,
.quizResults {
  zoom: 1;
}

.quizArea:before,
.quizArea:after,
.quizResults:before,
.quizResults:after {
  content: " ";
  display: block;
  height: 0;
  visibility: hidden;
  font-size: 0;
}

.quizArea:after,
.quizResults:after {
  clear: both;
}

.questionCount {
  margin-bottom: 35px;
  font-size: 14px;
  font-style: italic;
}

.questionCount span {
  font-weight: bold;
}

ol.questions {
  margin-top: 20px;
  margin-left: 0;
}

ol.questions li {
  margin-left: 0;
}

ul.answers {
  margin-left: 20px;
  margin-bottom: 20px;
}

ul.responses li {
  margin: 10px 0 20px;
}

ul.responses li p span {
  display: block;
  font-weight: bold;
  font-size: 30px;
}

